class MapDataModel {
  get map() {
    return map;
  }
  set map(leafletMap) {
    map = leafletMap;
  }
  get CRS() {
    return map.options.crs;
  }
  get plazaID() {
    return plazaID
  }
  set plazaID(id) {
    plazaID = id
  }
  get buildingID() {
    return buildingID;
  }
  set buildingID(id) {
    buildingID = id;
  }
  get mapCenterPoint() {
    return mapCenterPoint;
  }
  set mapCenterPoint(point) {
    mapCenterPoint = point;
  }
  getMapData(plazaID, floor) {
    const key = `${plazaID}-${floor}`;
    return mapDataCache[key];
  }
  setMapData(plazaID, floor, data) {
    const key = `${plazaID}-${floor}`;
    mapDataCache[key] = data;
  }
}

export default new MapDataModel();


