/**
 * 
 * @private 
 * @module SearchAction
 */


import Store from '../ui/store/Store';

/**
 * 
 * 
 * @param {String} name 
 * @param {GeoJson} featureGroup 
 * @return {Array}
 * @memberof SearchAction#
 */
function searchStoreByName(name, featureGroup) {
  return checkStore(featureGroup, feature => {
    return includes(feature.properties.re_name, name);
  });
}
/**
 * id
 * 
 * @param {String} id 
 * @return {Array}
 * @memberof SearchAction#
 */
function searchStoreByID(id, featureGroup) {
  return checkStore(featureGroup, feature => {
    return feature.id == id;
  });
}
/**
 * , 
 * 
 * @param {GeoJson} featureGroup 
 * @param {Function} fn  
 * @return {Array} 
 * 
 * @memberof SearchAction#
 */
function checkStore(featureGroup, fn) {
  const featureList = [];
  if (featureGroup) {
    featureGroup.eachLayer(feature => {
      const properties = feature.feature.properties;
      if (fn(feature.feature)) {
        featureList.push(new Store(feature));
      }
    });
  }
  return featureList;
}

/**
 * 
 * 
 * @param {String} storeName 
 * @param {String} searchName 
 * @memberof SearchAction#
 */
function includes(storeName, searchName) {
  storeName = storeName.toUpperCase();
  searchName = searchName.toUpperCase();
  if (String.prototype.includes) {
    return storeName.includes(searchName);
  } else {
    return storeName.indexOf(searchName) > -1;
  }
}

export {
  searchStoreByName,
  searchStoreByID
}


