/**
 * loading 
 * 
 * @private
 * @module ui/loading/index
 */

import './loading.less';
import { EventTypes } from '../../events';
import { mapDataService } from '../../services';

/**
 * , , loading
 * 
 * @param {LeafletMap} name 
 */
let loadingPane = null;
function initialize(map) {
  loadingPane = map.createPane('loading');
  map.getContainer().appendChild(loadingPane);
  mapDataService.on(EventTypes.GET_MAP_DATA_START, show);
  mapDataService.on(EventTypes.GET_MAP_DATA_SUCCESS, hide);
  mapDataService.on(EventTypes.GET_MAP_DATA_ERROR, hide);
}

/**
 * loading
 * 
 * @memberof ui/loading/index#
 */
function show() {
  if(loadingPane) {
    loadingPane.style.display = 'block';
    loadingPane.addEventListener('dblclick', event => {
      event.stopPropagation();
    });
  }
}

/**
 * loading
 * 
 * @memberof ui/loading/index
 */
function hide() {
  if(loadingPane) {
    loadingPane.style.display = 'none';
  }
}

export default { 
    initialize,
    show,
    hide 
}


