const defaultConfig = {
  /**
   * congrol
   * 
   * @type {String}
   */
  position: 'bottomleft',
  /**
   * 
   * 
   * @type {String}
   */
  prefixFloor: 'F',
  /**
   * 
   * 
   * @type {String}
   */
  prefixBasement: 'B',
  /**
   * , , 
   * 
   * @tytype {Int}
   */
  defaultFloor: 1
};

export default defaultConfig;

export function merge(config) {
  Object.assign(defaultConfig, config);
}


