/**
 * Marker
 */

import Layer from '../Layer';
import { createMarker, createIcon } from './creator';

/**
 * Marker
 * 
 * @export
 * @class Marker
 * @extends {Layer}
 * 
 * @param {Array|LatLng} pos 
 * @param {Object} options description
 * 
 * @example 
 * 
 * new Marker([115, -70], {
 *  icon: {
 *    iconUrl: 'http://.....'
 *  }
 * });
 */
export default class Marker extends Layer {
  constructor(pos, options = {}) {
    super(options);
    this._marker = createMarker(pos, options);
    this.layerDisplay = this._marker;
    this.injectDispatcher(this._marker);
  }
  /**
   * 
   *  
   * @private 
   * @param {LeafletMap} map 
   * 
   * @memberOf Marker#
   */
  onAdd(map) {
    if (this._marker) {
      this._marker.addTo(map);
    }
  }
  /**
   * 
   * 
   * @private 
   * @memberOf Marker#
   */
  onRemove() {
    if (this._marker) {
      this._marker.remove();
    }
  }
  /**
   * 
   * 
   * @param {Boolean} draggable 
   * @memberof Marker#
   */
  setDraggable(draggable) {
    if (draggable) {
      this._marker.dragging.enable();
    } else {
      this._marker.dragging.disable();
    }
  }
  /**
   * 
   * 
   * @param {Object} icon icon
   * @see http://leafletjs.com/reference-1.0.3.html#icon
   * 
   * @memberOf Marker#
   */
  setIcon(icon) {
    this._marker.setIcon(createIcon(icon));
  }
  /**
   * icon
   * 
   * @override 
   * @memberOf Marker#
   */
  getElement() {
    return this._marker._icon;
  }
}


