const FLOOR_CHANGE = 'floorchange';
const DRAG_START = 'dragstart';
const DRAG_END = 'dragend';
const RENDERED = 'rendered';
const GET_MAP_DATA_SUCCESS = 'getMapDataSuccess';
const GET_MAP_DATA_ERROR = 'getMapDataError';
const GET_MAP_DATA_START = 'getMapDataStart';
const FEATURE_CLICK = 'featureClick';

/**
 * , (), `FFanMap`
 * 
 * @example 
 * 
 * ffanMap.on(FFanMap.EventTypes.RENDERED, function(event){});
 * 
 * @module  EventTypes
 */
export {
  /**
   * , 
   * 
   * @property {object} event 
   * 
   * | property | description |
   * | :------- | : --------- |
   * | regionGroup | region,  `FeatureGroup` 
   * | commonMarkerGroup | SDKmarker 
   * 
   * @readonly 
   * @enum {string}
   * @member {string} RENDERED
   * @example
   * 
   * ffanMap.on(FFanMap.EventTypes.RENDERED, function(event){
   *  console.log(event.regionGroup, event.commonMarkerGroup);
   * }); 
   * 
   * @see http://leafletjs.com/reference-1.0.3.html#featuregroup
   * @see http://leafletjs.com/reference-1.0.3.html#layergroup
   */
  RENDERED,
  /**
   * 
   * 
   * @readonly 
   * @enum {string}
   * @member {string} GET_MAP_DATA_SUCCESS
   */
  GET_MAP_DATA_SUCCESS,
  /**
   * 
   * 
   * @readonly 
   * @enum {string}
   * @member {string} GET_MAP_DATA_ERROR
   */
  GET_MAP_DATA_ERROR,
  /**
   * 
   * 
   * @readonly 
   * @enum {string}
   * @member {string} GET_MAP_DATA_START
   */
  GET_MAP_DATA_START,
  /**
   * , , , RENDERED
   * 
   * @readonly 
   * @enum {string}
   * @member {string} FLOOR_CHANGE
   * @see EventTypes|RENDERED
   */
  FLOOR_CHANGE,
  /**
   * 
   * 
   * @readonly 
   * @enum {string}
   * @member {string} DRAG_START
   */
  DRAG_START,
  /**
   * 
   * 
   * @readonly 
   * @enum {string}
   * @member {string} DRAG_START
   */
  DRAG_END,
  /**
   * , Region
   * 
   * @readonly 
   * @enum {string}
   * @member {string} DRAG_START
   */
  FEATURE_CLICK
};


