/**
 * @private
 * @module  utils/mapDataFormat
 */
import L, { Util, LatLng } from 'leaflet';
import { mapDataModel } from '../models';

/**
 * , bkfloor
 * 
 * @export
 * @param {Object} mapData 
 */
export function getMapCenterPoint(mapData) {
  const original = mapData.features[0]['geometry']['coordinates'][0][0][0];
  return [parseFloat(original[0]), parseFloat(original[1])];
}

/**
 * , 
 * 
 * @export
 * @param {Object} mapData 
 * @param {Array} centerPoint 
 * @memberof utils/mapDataFormat
 */
export function formatData(mapData, centerPoint) {
  for (let i = 0; i < mapData.features.length; ++i) {
    var coordinatesList = mapData.features[i]['geometry']['coordinates'][0][0];
    for (let j = 0; j < coordinatesList.length; ++j) {
      coordinatesList[j] = [ Number(coordinatesList[j][0]) - Number(centerPoint[0]),  Number(coordinatesList[j][1]) - Number(centerPoint[1])];
    }
    const element = mapData.features[i];
    if (element.hasOwnProperty('properties')) {
      const properties = element.properties;
      // if (properties.hasOwnProperty('centerx')) { properties.centerx = Number(properties.centerx) - Number(centerPoint[0]); }
      // if (properties.hasOwnProperty('centery')) { properties.centery = Number(properties.centery) - Number(centerPoint[1]); }
    }
  }
  return mapData;
}

/**
 * , Latlng, Mercator, 
 *
 * @param {Array|LatLng} markerPosData
 * @return {L.Point}
 */
export function getLatlng(markerPosData) {
  if (Util.isArray(markerPosData)) {
    const centerPoint = mapDataModel.mapCenterPoint;
    return unproject(new L.Point(parseFloat(markerPosData[0]) - centerPoint[0], parseFloat(markerPosData[1]) - centerPoint[1]));
  }
  if (markerPosData instanceof LatLng) {
    return markerPosData;
  }
  return null;
}
function unproject(point) {
  return mapDataModel.CRS.projection.unproject(point);
}


