import Control from '../Control';
import doT from 'dot/doT.js';
import template from './tpl.html';
import './floorcontrol.less';
import GlobalConfig from '../../configs';
import { EventTypes } from '../../events';

export default class FloorControl extends Control {
  constructor() {
    super(GlobalConfig.floor.position);
  }
  /**
   * 
   * 
   * @param {Array.<Int>} floorList
   * @param {number} [defaultFloor=1]
   * @memberof FloorControl#
   */
  activate(floorList, defaultFloor = 1) {
    floorList.sort((x, y) => parseFloat(y) - parseFloat(x));
    this._floorList = this._formatData(floorList);
    this._maxFloor = this._floorList.length - 1;
    this._minFloor = 0;
    /**
     * (), 
     * @private 
     * @member
     * @type {Int}
     */
    // this._currentPosition = floorList.indexOf(GlobalConfig.floor.defaultFloor);
    this._currentPosition = this._getFloorPosition(this._floorList, defaultFloor);
    this._currentPosition = this._currentPosition == -1 ? 0 : this._currentPosition;
  }
  /**
   * , , 
   * 
   * @param {Int} floor 
   * , , **** 
   * 
   * @memberOf FloorControl#
   */
  choose(floor) {
    floor = parseInt(floor)
    if (!this._floorList || isNaN(floor)) return;
    const floorElement = this._floorList.filter(element => {
      return element.num === floor;
    });
    if (floorElement.length > 0 && floorElement[0].index != this._currentPosition) {
      this._clearSelected(this._container);
      this._changeFloor(floorElement[0].index);
    }
  }
  /**
   * 
   * 
   * @param {leafletMap} map 
   * @return {HTMLElement}
   * @memberof FloorControl#
   */
  onAdd(map) {
    const 
      tempComplier = doT.template(template),
      container = L.DomUtil.create('div', 'indoor-control-floor');
    if (this._floorList && this._floorList.length > 0) {
      container.innerHTML = tempComplier({floorList: this._floorList, current: this._floorList[this._currentPosition].num});
    }
    this._container = container;
    this._addEvent(container);
    this._checkBtnState();
    return container;
  }
  /**
   * simple destructor
   * 
   * @param {leafletMap} map 
   * @memberof FloorControl#
   */
  onRemove(map) {
    this._removeEvent();
    this._floorList = [];
    this._container = null;
  }
  /**
   * , UI
   * 
   * @memberOf FloorControl#
   */
  updateUI() {
    const tempComplier = doT.template(template);
    if (this._floorList && this._floorList.length > 0) {
      this._container.innerHTML = tempComplier({floorList: this._floorList, current: this._floorList[this._currentPosition].num});
      // this._container.innerHTML = tempComplier({floorList: this._floorList, current: this._floorList[this._currentPosition].num});
      const element = this._container.querySelector(`a.floor-btn[data-index="${this._currentPosition}"]`);
      this._scrollTo(element);
    }
  }
  /**
   * 
   * 
   * @private 
   * @param {Array.<Int>} dataList 
   * @return {Array.<Object>}
   * @memberof FloorControl#
   */
  _formatData(dataList) {
    const list = [];
    dataList.forEach((num, index) => {
      // , , 1.5, 
      num = parseFloat(num);
      num = num - Math.floor(num) > 0 ? num : Math.floor(num);
      //
      const name = num > 0 ? `${GlobalConfig.floor.prefixFloor}${num}` : `${GlobalConfig.floor.prefixBasement}${Math.abs(num)}`;
      list.push({num, name, index});
    });
    return list;
  }
  /**
   * ,  =.=
   * 
   * @private
   * @param {HTMLDivElement} container 
   * @memberof FloorControl#
   */
  _addEvent(container) {
    L.DomEvent.on(container, 'click', event => {
       L.DomEvent.stopPropagation(event);
      const target = event.target;
      if (L.DomUtil.hasClass(target, 'floor-btn') && !L.DomUtil.hasClass(target, 'indoor-selected')) {
        this._clearSelected(container);
        this._changeFloor(this._getTargetFloor(target.dataset.index));
      }
    });
  }
  _removeEvent() {
    if (this._container) {
      L.DomEvent.off(this._container);
    }
  }
  /**
   * 
   * 
   * @private
   * @param {HTMLDivElement} container 
   * @memberof FloorControl#
   */
  _clearSelected(container) {
    const list = container.querySelectorAll('.indoor-selected');
    for (let index = 0; index < list.length; ++index) {
      const element = list[index];
      L.DomUtil.removeClass(element, 'indoor-selected');
    }
  }
  /**
   * , 
   * 
   * @private
   * @param {String} floor 
   * @return {Int}
   * @memberof FloorControl#
   */
  _getTargetFloor(floor) {
    let targetFloor = null;
    if (floor === '+' && this._currentPosition + 1 <= this._maxFloor) {
      return this._currentPosition + 1;
    }
    if (floor === '-' && this._currentPosition - 1 >= this._minFloor) {
      return this._currentPosition - 1;
    }
    if (targetFloor === null && !isNaN(parseInt(floor))) {
      return parseInt(floor);
    }
    return targetFloor;
  }
  /**
   * 
   * 
   * @private
   * @param {Int} floor 
   * @memberof FloorControl#
   */
  _changeFloor(floor) {
    if (floor === null) {
      console.error('change floor, target floor is null');
      return;
    }
    this._currentPosition = floor;
    this._checkBtnState();
    const element = this._container.querySelector(`a.floor-btn[data-index="${this._currentPosition}"]`);
    this._scrollTo(element);
    L.DomUtil.addClass(element, 'indoor-selected');
    this.fire(EventTypes.FLOOR_CHANGE, {floor: this._floorList[this._currentPosition].num});
    if (this._map) this._map.fire(EventTypes.FLOOR_CHANGE, {floor: this._floorList[this._currentPosition].num});
  }
  /**
   * updown
   * 
   * @private
   * @memberof FloorControl#
   */
  _checkBtnState() {
    const upBtn = this._container.querySelector('.control-floor-plus');
    const downBtn = this._container.querySelector('.control-floor-minus');
    if (this._currentPosition >= this._maxFloor) {
      L.DomUtil.addClass(downBtn, 'indoor-selected');
    } else {
      L.DomUtil.removeClass(downBtn, 'indoor-selected');
    }
    //
    if (this._currentPosition <= this._minFloor) {
      L.DomUtil.addClass(upBtn, 'indoor-selected');
    } else {
      L.DomUtil.removeClass(upBtn, 'indoor-selected');
    }
  }
  /**
   * 
   * 
   * @private
   * @param {HTMLAnchorElement} element 
   * @memberof FloorControl#
   */
  _scrollTo(element) {
    const list = this._container.querySelector('#floorList');
    let distance = element.offsetTop - list.offsetHeight + element.clientHeight;
    distance = distance < 0 ? 0 : distance;
    list.scrollTop = distance;
  }
  /**
   * , , 
   * 
   * @param {Array} floorList 
   * @param {Int} currentFloor 
   * @return {Int} , -1
   * 
   * @memberOf FloorControl
   */
  _getFloorPosition(floorList, currentFloor) {
    const result = floorList.filter(item => {
      return parseFloat(item.num) == parseFloat(currentFloor);
    });
    if (result.length < 1) {
      return -1;
    }
    return result[0].index;
  }
}


