/**
 * , bk, fn, poi 
 */

import { LayerGroup, CRS, Point, DomUtil } from 'leaflet';
import GeoRender from './GeoRender';
import { PoiLabel } from '../../ui/label';
import POIMarker from '../../ui/marker/POIMarker'
import GlobalConfig from '../../configs';
import { mapDataModel } from '../../models';
import { mapDataUtils } from '../../utils';
import './indoor.less';

/**
 * , 3
 *
 * @export
 * @class IndoorRender
 * @extends {GeoRender}
 * @private
 */
export default class IndoorRender extends GeoRender {
  /**
   * label
   * 
   * @override
   * @protected 
   * @param {leaflet} map
   * @memberof IndoorRender#
   */
  initialize(map) {
    super.initialize(map);
    this._labelGroup = new LayerGroup();
    this._labelGroup.addTo(map);
    //
    this._markerGroup = new LayerGroup();
    this._markerGroup.addTo(map);
  }

  /**
   * 3
   *
   * @override
   * @param {Object} data  3, bk, fn, poi. 
   * @param {Object} option , , , , merge
   * @memberof IndoorRender#
   */
  render(data, option) {
    data.options = option || {};
    this.clear();
    return Promise
      .resolve(data)
      .then(this._renderBK.bind(this))
      .then(this._renderRegion.bind(this))
      // .then(this._renderFN.bind(this))
      // .then(this._renderPOI.bind(this))
      .catch(errorMsg => {
        console.error(errorMsg);
      });
  }
  /**
   * Label
   *
   * @override
   * @memberof IndoorRender#
   */
  clear() {
    super.clear();
    this._labelGroup.clearLayers();
    this._markerGroup.clearLayers();
  }
  /**
   * BK
   *
   * @private
   * @param {Object} mapData
   * @return {Promise}
   */
  _renderBK(mapData) {
    const self = this;
    return this._renderIndoorLayer(mapData, 'floor', {
      onEachFeature(feature, layer) {
        layer.setStyle({className: self._getClassNameByID('10000')});
      },
      crs: mapDataModel.CRS
    })
      .then(layer => {})
      .then(() => mapData);
  }
  /**
   * draw region
   * 
   * @private 
   * @param {Object} mapData 
   * @return {Promise}
   * 
   * @memberOf IndoorRender#
   */
  _renderRegion(mapData) {
    const self = this;
    return this._renderIndoorLayer(mapData, 'region', {
      onEachFeature(feature, layer) {
        const regionType = feature.properties.re_type;
        layer.setStyle({className: self._getClassNameByID(regionType)});
        if (GlobalConfig.commonMarker.hasMarkerConfig(regionType)) {
          // 
          self._createCommonMarker(feature, layer);
        } else {
          //label
          self._createLabel(feature, layer);
        }
      },
      interactive: self._map.options.regionInteractive,
      crs: mapDataModel.CRS
    })
      .then(layer => layer);
  }
  /**
   * POILabel
   * 
   * @private
   * @param {Object} feature 
   * @param {Polygon} layer labelpoi
   * 
   * @memberOf IndoorRender
   */
  _createLabel(feature, layer) {
    const name = feature.properties["re_name"];
    if (name && name != '' && name != '0' && this._map.options.showReginName) {
      let label = new PoiLabel([feature.properties.centerx, feature.properties.centery], name, layer);
      this._labelGroup.addLayer(label);
      layer.label = label;
    }
    // layer.poi_id = feature.id
  }
  _createCommonMarker(feature, layer) {
    const regionType = feature.properties.re_type;
    const markerConfig = GlobalConfig.commonMarker.getMarkerConfig(regionType);
    const marker = new POIMarker([feature.properties.centerx, feature.properties.centery], {
      icon: {
        iconUrl: markerConfig.url,
        iconSize: [16, 16],
        iconAnchor: [8, 8]
      },
      relativeLayer: layer
    });
    marker.markerData = Object.assign({}, feature);
    this._markerGroup.addLayer(marker);
  }
  /**
   * 
   *
   * @private
   * @param {Object} mapData render
   * @param {String} layerName  bk, fn, poi
   * @param {Object} option  see L.geoJson()
   * @return {Promise}
   *
   * @memberOf IndoorRender
   */
  _renderIndoorLayer(mapData, layerName, option) {
    return new Promise((resolve, reject) => {
      if (mapData.hasOwnProperty(layerName)) {
        super.render(mapData[layerName], option)
          .then(layer => { resolve(layer); });
      } else {
        reject(`${layerName} data is null`);
      }
    });
  }
  _getClassNameByID(type) {
    const prefix = 'ffan-region';
    const idInt = parseInt(type);
    if (isNaN(idInt)) { return prefix }
    const parentID = Math.floor(type * 0.0001) * 10000;
    const classList = [prefix, `${prefix}-${idInt}`, `${prefix}-${parentID}`];
    if (this._isStore(type)) classList.push('region-store');
    return classList.join(' ');
  }
  _isStore(regionType) {
    if (regionType >= 60000) {
      return true;
    }
    return false;
    }
}



